body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #21282E;
}

#root {
  position: absolute;
  background-image: url('./images/anchor.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.app {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

#acharya {
  width: 480px;
  padding: 80px;
}

button {
  background-color: #fff;
  border: 1px solid #21282E;
  font-size: 16px;
  border-radius: 4px;
  padding: 12px 34px;
  cursor: pointer;
}

textarea {
  outline: none;
  width: 100%;
  height: 100%;
  font-family: system-ui;
  border-radius: 4px;
  max-width: 400px;
  display: block;
  border: 0;
  background-color: hsl(208 16% 40% / 0.4);
  color: white;
  padding: 19px 34px;
  font-size: 16px;
  margin-bottom: 20px;
  resize: none;
}

.messages {
  overflow: scroll;
  height: calc(100vh - 180px);
}

.message {
  background-color: hsl(208 16% 40% / 0.4);
  border: 0;
  color: white;
  padding: 28px 34px;
  font-size: 16px;
  margin: 0 auto;
  margin-bottom: 20px;
  display: block;
  max-width: 400px;
  font-family: system-ui;
  border-radius: 4px;
  text-align: left;
}
.message-human {
  background-color: hsl(0deg 0% 5% / 75%);
}
.overlay {
  position: absolute;
  text-align: center;
  width: 100%;
  height: 100vh;
}

body {
  background: radial-gradient(ellipse at bottom, #21282E 0%, #1b2735 100%);
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.night {
  position: relative;
  width: 100%;
  height: 100%;
  transform: rotateZ(45deg);
}

.shooting_star {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 2px;
  background: linear-gradient(-45deg, #5f91ff, rgba(0, 0, 255, 0));
  border-radius: 999px;
  filter: drop-shadow(0 0 6px #699bff);
  -webkit-animation: tail 2000ms ease-in-out infinite, shooting 2000ms ease-in-out infinite;
          animation: tail 2000ms ease-in-out infinite, shooting 2000ms ease-in-out infinite;
}
.shooting_star::before {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  right: 0;
  height: 2px;
  background: linear-gradient(-45deg, rgba(0, 0, 255, 0), #5f91ff, rgba(0, 0, 255, 0));
  transform: translateX(50%) rotateZ(45deg);
  border-radius: 100%;
  -webkit-animation: shining 2000ms ease-in-out infinite;
          animation: shining 2000ms ease-in-out infinite;
}
.shooting_star::after {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  right: 0;
  height: 2px;
  background: linear-gradient(-45deg, rgba(0, 0, 255, 0), #5f91ff, rgba(0, 0, 255, 0));
  transform: translateX(50%) rotateZ(45deg);
  border-radius: 100%;
  -webkit-animation: shining 2000ms ease-in-out infinite;
          animation: shining 2000ms ease-in-out infinite;
  transform: translateX(50%) rotateZ(-45deg);
}
